@import "./style/scss/variables/color.scss";
@import "./style/scss/mixins.scss";

#app {
  // max-width: 850px;
  margin: 0 auto;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.table thead th,
.table th,
.table td,
button {
  border: medium none black;
}

.tm-nav__link,
.tm-right-nav__link,
.tm-cart,
.tm-product-card__hover-image,
.tm-product-card__image,
.tm-store__right-nav--link,
input[type="text"],
.tm-remove-btn,
.tm-checkout__step,
.tm-checkout__step-number,
.tm-checkout__step-label {
  transition: all 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.tm-product__info h3 a {
  color: $pureBlack;
}

.opacity1 {
  opacity: 1;
}

.opacity0 {
  opacity: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.green {
  color: $green;
}

.red {
  color: $red;
}

a {
  color: $black2;
  outline: 0;

  &:hover {
    color: $black2;
    outline: 0;
    text-decoration: none;
  }
}

a,
button {
  user-select: none;
}

.Toastify__toast--default {
  color: $pureWhite;
  background-color: $black2;

  padding: 15px;
}

.disable {
  pointer-events: none;
  opacity: 0.5;
}

.scale-when-hover {
  transition: all 0.2s ease-in-out;

  &:hover {
    transform: scale(1.05);
  }
}

h6 {
  margin: 0;
}

//remove increase and decrease btn in number input
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
}

input[type="text"] {
  display: inline-flex;
  padding: 0 14px;

  max-width: 100%;
  height: 42px;

  @include fontSize(14px);
  color: #737373;
  background: $pureWhite;
  border: 1px solid $white1;

  outline: 0;
  border-radius: 0;
  box-shadow: none;

  &:focus {
    border-color: $black2;
    color: $black2;
  }
}
