.tm-product-images {
  max-width: 35%;
  height: 100%;
  margin-top: -7.5vh;
  padding-bottom: 20px;
  // padding-right: 5px;
  // overflow: auto;
  // max-height: 100%;

  &::-webkit-scrollbar {
    width: 7px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    border-radius: 5px;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #c1c1c1;
    border-radius: 10px;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #a7a7a7;
  }

  li {
    img {
      width: 100%;
    }
  }

  li:not(:first-child) img {
    padding-top: 20px;
  }
}
.tm-product-details {
  @include fontSize(13px);

  max-width: 30%;
  height: 100%;
  padding-bottom: 20px;
  text-align: center;

  // position: sticky;
  // top: 0;

  h1 {
    @include fontSize(20px);

    width: 100%;
    padding: 5px 0;
    text-align: center;
  }

  &__quantity {
    display: flex;
    justify-content: center;

    input {
      width: 50%;
      min-width: 40px;
      text-align: center;
    }
  }

  &__operators {
    @include blackBackground();
    padding: 5px;
    cursor: pointer;
    user-select: none;
  }

  &__add-cart-btn {
    @include blackBackground();
    @include fontSize(24px);
    padding: 5px 25px;
  }

  td {
    vertical-align: middle;
  }
}

