.tm-product-card {
  width: calc(100% / 3);
  margin-bottom: 40px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  &__image {
    width: 100%;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    opacity: 1;

    &:hover {
      opacity: 0;
    }
  }

  &__image-container {
    height: 350px;
    width: 100%;

    padding: 0 15px;

    position: relative;
    overflow: hidden;

    a {
      position: relative;
      width: 100%;
      height: 100%;
      display: inline-block;
    }
  }

  &__hover-image {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;

    width: 100%;
    height: 100%;

    opacity: 0;
    background-size: cover;
    background-position: center;

    &:hover {
      opacity: 1;
    }
  }

  &__info {
    h3 {
      font-weight: bold;
      font-size: 11px;
      letter-spacing: 0px;
    }
  }

  &__price {
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0px;
  }
}
