.page-animation {
  // position: fixed;
  transition: opacity 0.2s ease-in-out;

  &.page-animation-enter {
    opacity: 0.01;
  }

  &.page-animation-enter-active {
    opacity: 1;
    transition: opacity 1s ease-in-out;
  }

  &.page-animation-exit {
    opacity: 0.01;
  }
}

.page-animation-appear {
  opacity: 0.01;

  &.page-animation-appear-active {
    opacity: 1;
    transition: opacity 500ms ease-in;
  }
}
