.tm-checkout {
  width: 70%;

  &__steps-container {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
  }

  &__step {
    flex: 1;
    text-align: center;
    background-color: #f8f8f8;
    padding: 15px 0;
    border: 1px solid #ececec;
    cursor: pointer;

    &--active,
    &:hover {
      background-color: white;

      .tm-checkout__step-number {
        background-color: black;
      }

      .tm-checkout__step-label {
        color: black;
      }
    }
  }

  &__step-number {
    height: 30px;
    width: 30px;
    background-color: #cdcdcd;
    border-radius: 50%;
    display: inline-block;
    line-height: 30px;
    margin-right: 15px;
    color: white;
  }

  &__step-label {
    color: #969696;
    text-transform: uppercase;
    font-weight: 700;
  }

  &__content {
    padding: 15px;
  }

  &__order-summary {
    margin-bottom: 15px;

    li{
      strong{
        padding-left: 5px;
      }
    }
  }

  &__order-details {
    width: 100%;

    tr {
      border-bottom: 1px solid #ececec;
    }

    .tm-checkout__order-product:not(:last-child) {
      border-bottom: none;
    }

    th {
      padding: 15px 0;
    }
  }
}
