
@import "./variables/color.scss";
@import "./mixins.scss";

.tm-button{
  @include fontSize(13px);
  line-height: 13px;
  font-weight: 500;
  font-family: inherit;
  padding: 0 26px;
  height: 42px;
  margin: 0;
  display: inline-flex;
  cursor: pointer;
  outline: 0;
  color: $pureWhite;
  background: $black2;
  text-shadow: none;
  justify-content: center;
  align-items: center;
  border: 0;
  box-shadow: none;
  border-radius: 0;

  &:hover{
    color: $pureWhite;
  }
}

.tm-remove-btn{
  cursor: pointer;
  font-size: 30px;
  color: rgba(0, 0, 0, 0.4);
  display: inline-block;
  
  &:hover{
    color: $pureBlack;
    transform: scale(1.2);
  }
}