@import "../../../style/scss/variables/color.scss";
@import "../../../style/scss/mixins.scss";

.tm-cart {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  height: 100%;
  z-index: 99;
  padding: 0;
  width: 320px;
  background: $pureWhite;
  right: 0;
  transform: translate(320px, 0);

  &--show {
    transform: translate(0, 0);
  }

  &__close-btn {
    float: right;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: calc(100% + 15px);
    top: 20px;
  }

  &__header {
    border-bottom: 1px solid $white1;
    padding: 30px;
  }

  &__product-list {
    padding: 30px 30px 0;
  }

  &__product {
    border-bottom: 1px solid $white1;
    padding-bottom: 19px;
    margin-bottom: 20px;

    display: flex;

    .product__image {
      width: 40px;
      height: 40px;
      margin: 0;
    }

    .product__content {
      padding: 0 15px;

      h5 {
        @include fontSize(13px);
        font-weight: 500;
        margin: 0;
        text-transform: uppercase;

        a {
          color: $black2;
        }
      }
    }

    .product__quantity {
      display: inline-flex;
      @include fontSize(13px);
      font-weight: 400;
      color: #141414;
      line-height: 14px;
    }

    .product__cross {
      margin: 0 5px;
      @include fontSize(12px);
      line-height: 14px;
      display: inline-flex;
    }

    .product__price {
      display: inline-flex;
      line-height: 14px;

      span {
        font-weight: 700;
      }
    }
  }

  &__subtotal {
    width: 100%;
    padding: 22px 30px;
    border-top: 1px solid $white1;
    font-size: 16px;
    color: #454545;
    font-weight: 600;

    span {
      float: right;
      font-weight: 300;
    }
  }

  &__actions {
    width: 100%;
    padding: 30px;
    background: $pureWhite;
    border-top: 1px solid $white1;

    a {
      width: 100%;
      @include fontSize(13px);
      @include blackBackground();
      line-height: 13px;
      padding: 0 45px;
      height: 52px;

      &:not(:first-child) {
        margin-top: 5px;
      }

      display: inline-flex;
      cursor: pointer;
      outline: 0;
      text-shadow: none;
      justify-content: center;
      align-items: center;
    }
  }

  &__overlay {
    background-color: rgba(0, 0, 0, 0.5);
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.tm-cart-details {
  height: 100vh;
  width: 70%;
  padding: 0 15px;

  display: flex;
  flex-direction: column;

  &__back-btn {
    padding-bottom: 15px;
    border-bottom: 1px solid #e5e5e5;
  }

  &__products-info {
    tr {
      border-bottom: 1px solid #e5e5e5;

      td {
        padding: 30px 0;

        input {
          display: inline-block;

          width: 37px;
          height: 37px;
          padding: 0;
          margin: 0 auto;

          border: 1px solid #dbdbdb;

          font-size: 14px;
          text-align: center;
          color: #737373;
          background: #fff;

          outline: 0;
          float: none;
        }
      }

      td:not(:first-child) {
        padding-left: 10px;
      }

      td:not(:last-child) {
        padding-right: 10px;
      }
    }
  }

  &__products-quantity{
    width: 135px;
  }

  &__bill-container {
    width: 65%;

    padding: 20px 0;

    align-self: flex-end;
  }

  &__bill-info{
    width: 100%;
    margin-bottom: 20px;

    td{
      text-align: right;
    }
  }

  &__func-btns{
    .tm-button{
      width: 100%;
    }
  }

  &__coupon-container {
    display: flex;
    justify-content: space-between;

    padding: 20px 0;
    border-bottom: 1px solid #e5e5e5;

    .tm-cart-details__coupon-code {
      width: 67.5%;
    }

    .tm-button {
      width: 27.5%;
    }
  }
}
