@import "../../style/scss/animations.scss";
@import "../../style/scss/variables/color.scss";
@import "../../style/scss/mixins.scss";

.tm-store {
  padding: 10vh 40px 0;

  min-height: 100vh;
  max-width: 100%;
  max-height: 100vh;

  display: flex;
  justify-content: space-between;

  overflow: auto;

  &__left-nav {
    position: sticky;
    top: 0;
  }

  &__right-nav {
    position: sticky;
    top: 0;
    text-transform: uppercase;

    &--list {
      display: block;
      list-style: none;
      margin: 0;
      text-align: right;
    }

    &--item {
      display: block;
      position: relative;

      .tm-store__right-nav--link {
        display: block;
        padding: 6px 0;
        color: $black2;
        line-height: 1;
        font-size: 10px;
        font-weight: 500;
        will-change: transform;
      }
    }

    &--link {
      color: $pureBlack;
      font-size: 14px;
      font-weight: 700;
      letter-spacing: 3.2px;
      padding: 0px;

      &:hover {
        color: $pureBlack;
        transform: translateX(4px);
      }
    }

    &--cart {
      border-radius: 0;
      font-weight: bold;
      margin: 10px 0 0;
      border: 2px solid #000;
      color: #000;
      background: #fff;
      position: relative;
      display: inline-block;
      vertical-align: middle;
      text-align: center;
      height: 2em;
      font-size: 1em;
      line-height: 1.5em;
      cursor: pointer;

      &::after {
        transition: height 0.1s ease-out;
        bottom: 100%;
        margin-bottom: 0;
        margin-left: -7px;
        height: 8px;
        width: 14px;
        left: 50%;
        content: " ";
        position: absolute;
        pointer-events: none;
        border: 2px solid #000;
        border-top-left-radius: 99px;
        border-top-right-radius: 99px;
        border-bottom: 0;
      }
    }
  }
}

.add-course {
  transform: translate3d(-100%, 0, 0);
  animation: slideInLeft 0.2s forwards;
  animation-delay: 1s;
}
